import Vue from 'vue'
import Vuex from 'vuex'

import allGoods from './allGoods'
import allOrder from './allOrder'
import allComments from './allComments'
import allAddress from './allAddress'

// vuex 
Vue.use(Vuex);

const state = { 
  imgUrl:'',
  // 
  goToType: '',
  // 
  goodsNum: 0,
  // id
  slideShowId: '',
  // id
  activityId: '',
  // id
  classId:'',
  // id
  goodsID:'',
   // 
  allGoods: allGoods,
  // 
  allOrder: allOrder,
  // 
  allComments: allComments,
  // 
  allAddress: allAddress,
  // 
  selGoods:{
    id:'',
    specificsChild:'',
    selected: true,
    proNum: 0,
    price: 0,
    img: '',
  },
  // 
  car: [],

  // 
  payGoods: [],
  // id
  odderDetailType: 0,
  // 
  addressObj:{},
  // 
  testPersonObj: {},
  // 
  orderObj: {},
  // 
  selfObj: {},

  // flag
  csFlag: true,
};
const mutations = {
  //  key 
  setId (state,obj) {
    let key = obj.key;
    state[key] = obj.value;
  },
  //  key 
  resetId (state,key) {
    state[key] = '';
  },
  // 
  setData(state, obj) {
    let key = obj.key;
    state[key] = obj.value;
  },
   // 
  carSet(state, obj) {
    // 
    if (!state.car) return; 

    let val = state.selGoods, param = 1; 
    if (obj.value) 
      val = obj.value, param = obj.value.proNum || 1;

    let gId = val.id, sId = val.specificsChild;
    
    // 
    let flag = true; 
    if(state.car.length > 0) {
      state.car.forEach((ele, index)=> {
        if (ele.id === gId && ele.specificsChild === sId) {
          flag = false; 
          if(obj.type) {
            ele['proNum'] += param;
            // 
            state.goodsNum += param;
          } else {           
            // 
            if(ele['proNum'] > param){
              ele['proNum'] -= param;
              state.goodsNum -= param;
            } else {
              state.goodsNum -= param
              state.car.splice(index,1);
            }
          }
        };
      });
    }
    // ,
    if(flag && obj.type) {
      state.goodsNum += param;
      val['proNum'] = param;
      state.car.push(val);
    }
    mutations.setId(state,{
      key:'csFlag',
      value: true
    });
  },
  // 
  add(state,obj){
    if (!state.csFlag) return;
    // 
    mutations.setId(state,{
      key:'csFlag',
      value: false
    });
    // state.goodsNum += 1;
    if (obj) {
      mutations.carSet(state, {type:true,value:obj});
    } else {
      mutations.carSet(state,{type:true,value:false});
    }
  },
  reduce(state,obj){
    if (!state.csFlag) return;
    // 
    mutations.setId(state,{
      key:'csFlag',
      value: false
    });
    // if (state.goodsNum > 0) {
    //   state.goodsNum -= 1;
    // }
    if (obj) {
      mutations.carSet(state, {type:false,value:obj});
    } else {
      mutations.carSet(state, {type:false,value:false});
    }
    
  },
  col() {
    alert(123);
  },
  
};
const actions = {
  
};


export default new Vuex.Store({
  state,
  mutations,
  actions   
});




