import Vue from 'vue'
import App from './App'
import store from './store/store'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import http from './untils/http'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import routesUrl from './router/routerUrl'

import './assets/js/phone.js'
import './assets/js/common.js'


Vue.use(VueRouter)
const router = new VueRouter({

routes: routesUrl,



linkActiveClass: 'active',



scrollBehavior (to, from, savedPosition) {
if(savedPosition) {
return savedPosition
} else {
if (from.meta.keepAlive) {
from.meta.savedPosition = document.body.scrollTop;
}
return { x: 0, y: to.meta.savedPosition ||0}
}
}
})


Vue.config.productionTip = false


Vue.prototype.$http = http;


FastClick.attach(document.body)


Vue.use(AlertPlugin)
Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)


/* eslint-disable no-new */
window.vm = new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



